.main-container {
    /* display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2em; */
    background-color: rgb(18, 217, 236);
    border-radius: 20px;
    margin: 2em auto auto;
    padding: 1em;
    /* width: 100%; */
}

h1 {
    font-family: 'Luckiest Guy', cursive;
    font-size: 3em;
    color: darkred;
}

.modal-overlay {
    z-index: 100000000;
    background-color: #353A40;
    opacity: .7;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.chosenBox {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    width: 70%;
    height: fit-content;
    margin: 1em 0;
}

.settings-box {
    background-color: aliceblue;
    /* width: 20em; */
    border-radius: 15px;
    padding: 1em;
    height: fit-content;
}

.buttons-box {
    display: flex;
    flex-direction: row;
}

.mathing-area {
    /* width: 25em; */
    margin: 2em;
    font-size: 1.5em;
}

.equation {
    display: flex;
    flex-direction: row;

}

.mathing-btn-input {
    display: flex;
    flex-direction: column;
}

/* .inputWithSave {
    display: flex;
    flex-direction: row;
} */

.submitBtn {
    font-size: .8em;
    background-color: #fff;
    color: #04b3d1;
    border-radius: 5px;
    border: #fff;
    margin: 1.5em .5em;
    padding: .5em;
    width: 5em;
}

.submitBtn:hover {
    background-color: #04b3d1;
    color: #fff;
}

.btnBox {
    display: flex;
    flex-direction: row;
    margin: 0 .5em 1em .5em;
}

.colorItem {
    display: flex;
    flex-direction: row;
    margin: .5em;
    width: fit-content;
    height: fit-content;
    padding: .5em;
    border: 1px solid oldlace;
    border-radius: 8px;
}

.colorItem:hover {
    border: 1px solid azure;
    border-radius: 8px;
    background-color: azure;
}

.colorName {
    margin: .5em;
}

.instructions {
    display: flex;
    justify-content: center;
    max-width: 20em;
    height: 15em;
    flex-direction: row;
}

span {
    cursor: pointer;
}

.mobileInstructions {
    margin: 0 1em 1em 1em;
}

.header {
    font-family: 'Monoton', cursive;
    font-size: 3em;
}

.active {
    padding: .5em;
    font-size: 1em;
    background-color: #04b3d1;
    color: #fff;
    border-radius: 5px;
    border: #04b3d1;
    margin: .5em;
    width: 5em;
}

.inactive {
    padding: .5em;
    font-size: 1em;
    background-color: #fff;
    color: #04b3d1;
    border-radius: 5px;
    border: #fff;
    margin: .5em;
    width: 5em;
    box-shadow: 2px 2px 10px #888888;
}
.inactive:hover {
    background-color: #04b3d1;
    color: #fff;
}

.disabled {
    font-size: .8em;
    background-color: #fff;
    color: gray;
    border-radius: 5px;
    border: #fff;
    margin: 1.5em .5em;
    padding: .5em;
    opacity: 0.2;
    width: 5em;
}

.swapBtn {
    font-size: .8em;
    background-color: #fff;
    color: #04b3d1;
    border-radius: 5px;
    border: #fff;
    margin: auto .5em;
    padding: 1em;
}

.swapBtn:hover {
    background-color: #04b3d1;
    color: #fff;
}

.infoIcon {
    width: 100px;
    height: 100px;
}

@media (max-width: 967.98px) {
    .instructions {
        width: 100%;
    }
    .chosenBox {
        display: grid;
        grid-template-columns: auto auto auto;
        width: 100%;
    }
    .mathing-area {
        font-size: 1.5em;
    }
}

@media (max-width: 624.98px) {
    .btnBox {
        display: grid;
        grid-template-columns: auto;
    }
    .swapBtn, .colorSelect {
        margin: .5em;
    }
    .chosenBox {
        display: grid;
        grid-template-columns: auto auto;
        width: 100%;
    }
    .instructions {
        display: none;
    }
    .equation {
        display: grid;
        flex-direction: 75% 25%;
    
    }
}

@media (min-width: 624.98px) {
    .mobileInstructions, .infoIcon {
        display: none;
    }
}